import React from 'react'
import instagram from '../Assets/Icons/instagram.svg';
import facebook from '../Assets/Icons/facebook.svg'
import twitter from '../Assets/Icons/twitter.svg'
import Logo from "../Assets/Logo.PNG"
const Footer = () => {

    return (
        <footer>
            <div class="aem-Grid aem-Grid--12">
                <div class="aem-GridColumn aem-GridColumn--default--3 aem-GridColumn--tablet--6 aem-GridColumn--phone--12">
                    <ul>
                        <li><strong>Account</strong></li>
                        <li>Sign In</li>
                        <li>Register</li>
                        <li>Order Status</li>
                    </ul>
                </div>
                <div class="aem-GridColumn aem-GridColumn--default--3 aem-GridColumn--tablet--6 aem-GridColumn--phone--12 ">
                    <ul>
                        <li><strong>About Us</strong></li>
                        <li>Our Story</li>
                        <li>Careers</li>
                        <li style={{ visibility: 'hidden' }}>hide</li>

                    </ul>
                </div>
                <div class="aem-GridColumn aem-GridColumn--default--3 aem-GridColumn--tablet--6 aem-GridColumn--phone--12">
                    <ul>
                        <li><strong>Help</strong></li>
                        <li>Contact Us</li>
                        <li>Order Status</li>
                        <li>Returns</li>
                    </ul>
                </div>
                <div class="aem-GridColumn aem-GridColumn--default--3 aem-GridColumn--tablet--6 aem-GridColumn--phone--12">
                    <ul>
                        <li><strong>Follow Us!</strong></li>
                        <li >Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore.</li>
                        <div>
                            <img src={instagram} className='footerIcons' />
                            <img src={facebook} className='footerIcons' />
                            <img src={twitter} className='footerIcons' />
                        </div>
                    </ul>
                </div>
            </div>
            <div className='lowerFooter'>
                <img src={Logo} />
                <p>&copy; Company Name Address Ave, City Name, State ZIP</p>
                <ul>
                    <li><a>Terms of Use</a></li>
                    <li><a>Privacy Policy</a></li>
                </ul>
            </div>
        </footer>
    )
}

export default Footer;